import { useEffect } from "react";
import { useForm, ValidationError } from '@formspree/react';
import AOS from 'aos';
const Contacto = () => {
    const [state, handleSubmit] = useForm("xayarnjr");
    useEffect(() => {
        AOS.init();
    }, []);
    return (
        <section id="Contacto" className="padding-section">
            <div id="div-form" data-aos="fade-up">
                <form onSubmit={handleSubmit}>
                    <input id="name" type="text" name="name" placeholder="Nombre" required/>
                    <ValidationError
                        prefix="Name"
                        field="name"
                        errors={state.errors}
                    />
                    <input id="email" type="email" name="email" placeholder="Email" required />
                    <ValidationError
                        prefix="Email"
                        field="email"
                        errors={state.errors}
                    />
                    <textarea id="message" name="message" placeholder="Mensaje..." required/>
                    <ValidationError
                        prefix="Message"
                        field="message"
                        errors={state.errors}
                    />
                    <button type="submit" disabled={state.submitting} className="btn-form hover">Enviar</button>
                </form>
                <div id="iconos-contacto">
                    <a id="enlace-linkedin" href="https://www.linkedin.com/in/brian-cabrera-b94305226/" target="_blank" rel="noopener noreferrer" ><i id="linkedin" className="fab fa-linkedin"></i></a>
                    <a id="enlace-github" href="https://github.com/brian432" target="_blank" rel="noopener noreferrer" ><i id="github" className="fab fa-github-square"></i></a>
                </div>
            </div>
            <h1 data-aos="fade-down" className="hover cursor">Contacto</h1>
            <a id="arrow-up" data-aos="fade" href="Inicio"><i className="fas fa-arrow-up hover"></i></a>
        </section>
    )
}

export default Contacto;