import { useState, useEffect } from "react";
const Header = () => {
    const nav=["Sobre-Mí", "Portafolio", "Contacto"];
    const [menu, setMenu] = useState("");
    const [scroll, setScroll]=useState("");
    const [mouseOver, setMouseOver ] = useState("");
    useEffect(()=>{
        window.addEventListener("scroll", (e)=>{
            window.scrollY >0 ? setScroll("fadeOut"):setScroll("fadeIn");
        });
    }, []);
    return (
        <header className={scroll!==""?scroll:""} id="Inicio">
            <div className="div-logo"><span className="hover cursor">{'<'}</span><span className="hover cursor">{'/>'}</span></div>
            <nav>
                <ul id="ul-visible">
                    {
                        nav.map((elemento, indice)=><li key={indice}><a onMouseOver={(e)=>setMouseOver(e.target.name)} onMouseOut={(e)=>setMouseOver("")} href={`#${elemento}`} name={elemento} className="hover"><span className={mouseOver===elemento?"visible":""}>{"<"}</span>{elemento}<span className={mouseOver===elemento?"visible":""}>{"/>"}</span></a></li>)
                    }
                </ul>
                <div id="hamburguesa" onClick={()=>menu===""?setMenu("encendido"):setMenu("")}>
                    <div className={menu==="" ? "":"linea1"}></div>
                    <div className={menu==="" ? "":"linea2"}></div>
                    <div className={menu==="" ? "":"linea3"}></div>
                </div>
                <ul id="ul-hamburguesa" className={`${menu===""?"no-visible":""}`}>
                    {
                        nav.map((elemento,indice)=><li key={indice}><a href={`#${elemento}`}>{elemento}</a></li>)}
                    
                </ul>
            </nav>
        </header>
    )
}
export default Header;