import { useEffect } from "react";
import AOS from 'aos';
const About = () => {
    useEffect(() => {
        AOS.init();
    }, []);
    return (
        <section id="Sobre-Mí" className="padding-section">
            <div data-aos="fade-up" className="about">
                <p id="descripcion" className="cursor">Hola! Mi nombre es Brian Cabrera, soy desarrollador Front-End y futuro Full-Stack.
                Estoy en continuo aprendizaje. Las tecnologias que utilizo 
                <i id="arrow" className="fas fa-arrow-down hover"></i>
                </p>
                <div id="div-iconos" className="cursor">
                    <i id="html5" className="fab fa-html5"></i>
                    <i id="css3" className="fab fa-css3-alt"></i>
                    <i id="js" className="fab fa-js"></i>
                    <i id="react" className="fab fa-react"></i>
                    <i id="bootstrap" className="fab fa-bootstrap"></i>
                    <i id="sass" className="fab fa-sass"></i>
                </div>
            </div>
            <h1 className="hover cursor" data-aos="fade-down">Sobre mí</h1>
        </section>
    )
}

export default About;