import { useEffect } from "react";
import AOS from 'aos';
const Portafolio = () => {
    const enlaces = [
        {
            "proyecto": "https://brian432.github.io/Movies-App/",
            "codigo":"https://github.com/brian432/Movies-App"
        },
        {
            "proyecto": "https://brian432.github.io/weather-app/",
            "codigo":"https://github.com/brian432/weather-app"
        },
        {
            "proyecto": "https://brian432.github.io/api-countries/",
            "codigo":"https://github.com/brian432/api-countries"
        },
        {
            "proyecto": "https://brian432.github.io/job-listings/",
            "codigo":"https://github.com/brian432/job-listings"
        },
        {
            "proyecto": "https://brian432.github.io/ip-address-tracker/",
            "codigo":"https://github.com/brian432/ip-address-tracker"
        },
        {
            "proyecto": "https://brian432.github.io/rock-paper-scissors/",
            "codigo":"https://github.com/brian432/rock-paper-scissors"
        }
    ];


    useEffect(() => {
        AOS.init();
    }, []);
    return (
        <section id="Portafolio" className="padding-section">
            <h1 data-aos="fade-right" className="hover cursor">Portafolio</h1>
            <div data-aos="fade-left" className="portafolios" >
                {
                    enlaces.map((elemento, indice) =>
                        <div id={`div${indice}`} key={indice} className="div-filter">
                            <a href={elemento.proyecto} target="_blank"><button className="btn hover">Ver proyecto</button></a>
                            <a href={elemento.codigo} target="_blank"><button className="btn hover">Ver codigo</button></a>
                        </div>)
                }
            </div>
        </section>
    )
}

export default Portafolio;