import { useEffect } from 'react';
import AOS from 'aos';
const Inicio = () => {
    useEffect(() => {
        AOS.init({
            duration:2500,
        });
    }, []);
    return (
        <div id="parallax">
            <h1 data-aos="fade-right" className="hover cursor">Brian Cabrera</h1>
            <h2 data-aos="fade-left" className="hover cursor">Front-End Developer</h2>
        </div>
    )
}

export default Inicio;